import React from "react";
import { isValidUserLogin } from "../../../utills/helpers";
import { useDispatch, useSelector } from "react-redux";
import { requestLikePost, setModalState } from "../../../redux/actions";

export const PostListEngagement = ({
  totalViews,
  totalLikes,
  totalComments,
  postId,
}) => { 
  const { userDetails } = useSelector((state) => ({
    userDetails: state.authReducer.details,
  }));
  
  const dispatch = useDispatch();
  const likePost = () => {
    if (isValidUserLogin(userDetails)) {
      dispatch(
        requestLikePost({
          id: postId,
        })
      );
    }else{
      dispatch(
        setModalState({
          showLoginModal: true,
          showRegisterModal: false,
        })
      );
    }
  };
  return (
  <div className="rection-wrap">
    <a href="javscript:void(0);" className="d-inline-flex mr-3">
      <span>{totalViews || 0}</span>
      <i className="far fa-eye"></i>
    </a>
    <a href="javscript:void(0);" className="d-inline-flex mr-3" onClick={likePost}>
      <span>{totalLikes || 0}</span>
      <i className="far fa-thumbs-up"></i>
    </a>
    <a href="javscript:void(0);" className="d-inline-flex">
      <span>{totalComments || 0}</span>
      <i className="far fa-comment"></i>
    </a>
  </div>
)};
