import React from "react";
import { resolvePostImageURL } from "../../../utills/helpers";
export const SharePost = ({post}) => {
  const isMobileOrTablet = () => {
    return /(android|iphone|ipad|mobile)/i.test(navigator.userAgent);
  };

  const share = (e, platform) => {
    e.preventDefault();
    let { title, shortDescription, featuredImage, url } = post;
    const uri = `${process.env.REACT_APP_APP_ENDPOINT}${url}`;
    let description = shortDescription
      ? shortDescription.replace(new RegExp(/[ +!@#$%^&*().]/g), '_')
      : '';
    let image = featuredImage
      ? resolvePostImageURL(featuredImage)
      : null;
    title = title
      ? title.replace(new RegExp(/[ +!@#$%^&*().]/g), '_')
      : null;
    const URL = encodeURIComponent(
      `${process.env.REACT_APP_SOCIAL_SHARE_URL}?title=${title}&description=${description}&image=${image}&url=${uri}`,
    );

    let socialAppUrl = '';
    switch (platform) {
      case 'facebook':
        socialAppUrl = `https://www.facebook.com/dialog/share?app_id=${process.env.REACT_APP_FACEBOOK_APP_ID}&display=popup&href=${URL}`;
        break;
      case 'whatsapp':
        socialAppUrl = `https://${
          isMobileOrTablet() ? 'api' : 'web'
          }.whatsapp.com/send?text=${URL}`;
        break;
      case 'linkedin':
        socialAppUrl = `https://www.linkedin.com/shareArticle?url=${URL}`;
        break;
      case 'twitter':
        socialAppUrl = `https://twitter.com/share?url=${URL}`;
        break;
      default:
        break;
    }
    window.open(socialAppUrl);
  };
  return (
    <div className="social-media-listing d-flex mt-3">
      <span className="mr-2 social-icon">
        <i className="fas fa-share-alt"></i>
      </span>
      <span className="mr-2 social-icon" onClick={(e) => share(e,'facebook')}>
        <i className="fab fa-facebook-f"></i>
      </span>
      <span className="mr-2 social-icon" onClick={(e) => share(e,'twitter')}>
        <i className="fab fa-twitter"></i>
      </span>
      <span className="mr-2 social-icon" onClick={(e) => share(e,'whatsapp')}>
        <i className="fab fa-whatsapp"></i>
      </span>
      <span className="social-icon" onClick={(e) => share(e,'instagram')}>
        <i className="fab fa-instagram"></i>
      </span>
    </div>
  );
};
