import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { getFullName, getProfileImage } from "../../../../utills/helpers";
import { Row, Col, Badge, Spinner } from "react-bootstrap";
import Avatar from "react-avatar";
import { requestUserPortalLogin } from "../../../../redux/actions";
import MyPost from "../MyPost";
export const MyProfile = () => {
  const { details, isLoadingLoginReq } = useSelector(
    (state) => state.authReducer
  );
  const dispatch = useDispatch();
  const onEditProfile = () => {
    dispatch(requestUserPortalLogin());
  };
  return (
    <>
      <Row>
        <Col sm={8}>
          <h1>{getFullName(details)} &nbsp;</h1>
          <div>
            <i className="fa fa-envelope" /> {details.email}
          </div>
          {details.phoneNumber ? (
            <div>
              <i className="fa fa-phone-alt" /> {details.phoneNumber}
            </div>
          ) : null}
          <Badge
            variant="primary"
            className="text-white cursor-pointer"
            onClick={onEditProfile}
          >
            {isLoadingLoginReq ? (
              <>
                <Spinner animation="border" size="sm" /> Edit Profile
              </>
            ) : (
              <>Edit Profile</>
            )}
          </Badge>
        </Col>
        <Col sm={4}>
          <Avatar
            size={150}
            src={getProfileImage(details)}
            name={getFullName(details)}
            round
          />
        </Col>
      </Row>
      <Row>
        <Col md={12} className={"mt-5"}>
          <div className="ribbon-heading-wrap mb-4">
            <div className="ribbon-heading">
              <h2 className="mt-2">My Posts</h2>
            </div>
          </div>
          {/* <h2>My Posts</h2> */}
          <MyPost />
        </Col>
      </Row>
    </>
  );
};

export default MyProfile;
