import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { Col, Row } from "react-bootstrap";
import Avatar from "react-avatar";
import { requestAuthorDetails, requestAuthorPosts } from "../../../../redux/actions";
import { getProfileImage, getFullName } from "../../../../utills/helpers";
import { PostSection } from "./PostSection";
import Skeleton from "react-loading-skeleton";

let limit = 2;
export const PublicProfile = () => {
  const { id } = useParams();
  const [currentPage, setCurrentPage] = useState(1);
  const { details, isLoading } = useSelector((state) => state.authorDetailsReducer);
  const postData = useSelector(
    (state) => state.authorPostsReducer
  );
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(requestAuthorDetails({ id }))
  }, [id])
  useEffect(() => {
    dispatch(requestAuthorPosts({ id, currentPage, limit }))
  }, [currentPage])
  const onPageChanged = (activePage) => {
    if (activePage) {
      setCurrentPage(activePage)
    }
  }
  return <>
    {isLoading ? <Row><Col sm={8}>
      <div className={"text-left"}>
        <Skeleton height={25} /></div>
        <div className={'mt-1'}>
      <Skeleton height={30} /></div></Col><Col sm={4}><div className="author-profile-loader"><div className="user-icon-circle"></div></div></Col></Row> : <Row><Col sm={8}>
        <h1>{getFullName(details)} &nbsp;</h1>
        <div>{details.about}</div>
      </Col>
        <Col sm={4}>
          <Avatar
            size={150}
            src={getProfileImage(details)}
            name={getFullName(details)}
            round
          />
        </Col>
      </Row>}
    <Row>
      <Col md={12} className={"mt-5"}>
        <div className="ribbon-heading-wrap mb-4">
          <div className="ribbon-heading">
            <h2 className="mt-2">My Posts</h2>
          </div>
        </div>
        <PostSection postData={postData} limit={limit} currentPage={currentPage} onPageChanged={onPageChanged} />
      </Col>
    </Row>
  </>;
};

export default PublicProfile;
