import React from "react";
import { getPostURL, resolvePostImageURL } from "../../../utills/helpers";
import { Link } from "react-router-dom";
import { SharePost } from "./SharePost";
import { PostListEngagement } from "./PostListEngagement";
import { PostTags } from "./PostTags";

const PostComponent = (post) => (
  <>
    <div className="blog-list-wrap d-md-flex">
      <div className="post-thumbnail-wrap">
        <div className="post-thumbnail">
          <img
            src={resolvePostImageURL(post.featuredImage)}
            alt={"Blog Thumbnail"}
          />
        </div>
      </div>
      <div className="blog-list-content ml-md-3">
        <div className="ribbon-heading-wrap">
          <div className="ribbon-heading ligh-blue-ribbon">
            <h4 className="m-0">
              {post.categories.map((cat) => (
                <React.Fragment key={cat._id}>
                  <Link to={getPostURL(post)}>{cat.name}</Link>
                </React.Fragment>
              ))}
            </h4>
          </div>
        </div>
        <Link to={getPostURL(post)}>
          <h5>{post.title}</h5>
        </Link>
        <p className="mt-3">
          {post.shortDescription.truncate(100)}
          <Link to={getPostURL(post)}>...Read More</Link>{" "}
        </p>
        <PostListEngagement {...post} />
        <PostTags tags={post.tags} />
        <SharePost post={{ ...post, url: getPostURL(post) }} />
      </div>
    </div>
  </>
);

export default PostComponent;
