import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import PostLoader from "../../Common/PostLoader";
import PostComponent from "../../Common/Post";
import { requestMyPost } from "../../../../redux/actions";
import PaginationComponent from "../../Pagination";

let limit = 2;
const MyPost = () => {
    const { isLoading, posts, totalRecords } = useSelector(
        (state) => state.myPostReducer
    );
    const dispatch = useDispatch();
    const [currentPage, setCurrentPage] = useState(1);
    useEffect(() => {
        dispatch(requestMyPost({
            currentPage, limit
        }))
        //eslint-disable-next-line
    }, [currentPage]);
    const onPageChanged = (activePage) => {
        if (activePage) {
            setCurrentPage(activePage)
        }
    }
    return (
        <>
            {isLoading
                ? ["", "", "", ""].map((_, index) => <PostLoader key={index} />)
                : posts.length ? posts.map((post) => <PostComponent key={post._id} {...post} />) : <div className={"no-post-box"}><div className={'text-line'}>No post added yet</div></div>}
            {!isLoading && totalRecords > limit ? <PaginationComponent totalRecords={totalRecords} pageLimit={limit} currentPage={currentPage} onPageChanged={onPageChanged} /> : null}
        </>
    );
};

export default MyPost;